$card-width: 200px;
$card-height: 403px;
$card-gap: 48px;
$secondary-color: #00acdc;
$border-color: #f3f3f3;
$menu-height: 60px;
$imageWidth: 160px;
$imageHeight: 134px;

@mixin small-text {
  font-size: 11px;
  line-height: 13px;
}

@mixin normal_text {
  font-size: 12px;
  font-weight: 500;
}

@mixin strong-text {
  font-weight: 900;
  font-size: 16px;
  line-height: 19px;
}
